const msgObj={
    time:50,
    title:'',
    content:'[5][6]'
}
function countDown(){
    if(msgObj.time == 0){
        
        return
    }
    setTimeout(function(){
        msgObj.time --;
        countDown();
    },1000);
}

export default{
    name:'wolf',
    msgObj,
    countDown
}


