import Vue from 'vue'
import Vuex from 'vuex'

//Vuex
Vue.use(Vuex)

//state
const state = {
      //  domain:'http://127.0.0.1/' 
         //
        userInfo:{
        uid:null,
        accout:null,
        password:null,
        nick:null,
        ulevel:null,
        gold:null,
        flower:null,
        portrait:null
        },
        //title
        title:null
    }
    //
const mutations = {
        //
        updateUserInfo(state,newUserInfo){
            state.userInfo = newUserInfo;
        },
        updateTitle(state,newTitle){
            state.title = newTitle;
        }
    }

//
export default new Vuex.Store({
    state,
    mutations
})





